.landingPage {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .content {
    z-index: 10;
    text-align: center;
    color: white;
  }
  
  .title {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 2rem;
  }
  
  .ctaButton {
    background-color: white;
    color: #0d47a1;
    font-weight: bold;
    padding: 0.75rem 2rem;
    border-radius: 9999px;
    font-size: 1.25rem;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .ctaButton:hover {
    background-color: #e6e6e6;
  }
  
  @media (max-width: 768px) {
    .title {
      font-size: 2rem;
    }
  
    .ctaButton {
      font-size: 1rem;
      padding: 0.5rem 1.5rem;
    }
  }